import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import SEO from '@common/SEO';
import Fade from 'react-reveal/Fade';
//import DateCountdown from 'react-date-countdown-timer';
import styled from 'styled-components';

import Layout from '@common/Layout';
import Navbar from '@common/Navbar';
import Footer from '@common/Footer';
import Call2Act from '@ui/call2act';

import {Container, Section} from '@components/global';
import SectionHeader from '@ui/sectionheader';
import branding from '@static/og/branding.jpg';


const Index = () => (
  <StaticQuery
    query={graphql`
      query {
        allFile(filter: { sourceInstanceName: { eq: "proj" } }) {
          edges {
            node {
              relativePath
              childImageSharp {
                fluid(maxWidth: 400, maxHeight: 400) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
        },  
      }
    `}
    render={data => (
      <Layout>
        <SEO title="🔥Secret Discount of Hai🔥" desc="Get the exclusive secret discount of Hai" img={branding} url="https://rearia.io/legality/privacy"/>
        <Navbar />
        <Section id="contact">
          <SectionHeader 
            header="Here comes our secret offer 🔥"
            subheader="This page will redirect you to a secret early bird discount of Hai  when we launch!" 
          />
          <Container>
            <Fade>
            
            <Countdown>
              <small>We will launch at 12PM(GMT-4) on 15th July.</small><br/>
              {/* <b><DateCountdown dateTo='July 15, 2020 12:00:00 GMT-04:00' callback={()=>alert('Hello')} mostSignificantFigure="day"/> to go!</b>*/}
            </Countdown>

            <p>
            In the meantime, <b>please join our giveaway and get chances to win a Hai <i>FOR FREE</i></b> ! 
            </p>  
            <Call2Act text="Join our giveaway 💝" href="https://docs.google.com/forms/d/e/1FAIpQLSegYSr0DPJQSYW3kUn1rdl59v6B4YR-fUVw4EtHzjyidK8prA/viewform" target="_blank"/>
            </Fade>
          </Container>
        </Section>

        <Footer />
      </Layout>
    )}
  />
);



const Countdown = styled.div`
  display: inline-block;
  font-weight: 400;
  vertical-align: middle;
  line-height: 24px;
  padding: 11px 25px;
  margin: 30px 0;
  ${props => props.theme.font_size.large}; 
  color: ${props => props.theme.color.secondary.S900};   
  border-radius: 1px;
  border-left: 3px solid  ${props => props.theme.color.secondary.S900}; 

`;  

export default Index;
